@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff");
}
body {
  margin: 0 auto;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  body {
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  background: #ffffff;
  border: 2px solid #444444;
  box-sizing: border-box;
  color: #444444;
  width: 100%;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 15px;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
  box-sizing: border-box;
}
.button:active {
  outline: none;
  color: rgba(255, 255, 255, 0.3);
}
.button-arrow {
  position: relative;
  width: 50px;
  height: 26px;
  border: 1px solid #ececec;
  display: block;
  padding: 26px 26px;
}
.button-arrow:hover {
  background-color: rgba(255, 255, 255, 0);
}
@media (min-width: 768px) {
  .button-arrow {
    background-color: rgba(255, 255, 255, 0);
    border: none;
    padding: 0;
  }
}
.button-arrow svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #000000;
}
.button-arrow:hover svg {
  fill: rgba(0, 0, 0, 0.6);
}
.button-arrow:active svg {
  fill: rgba(0, 0, 0, 0.3);
}
.page__container {
  background-color: #ffffff;
  margin: 0 auto;
}
.menu-nav {
  position: absolute;
  width: 100%;
  top: 80px;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  z-index: 1;
}
.menu-nav--nojs {
  position: static;
}
@media (min-width: 768px) {
  .menu-nav {
    position: static;
    background-color: transparent;
    height: 100%;
  }
}
.menu-nav--closed .menu-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 17px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.menu-nav--opened .menu-nav__toggle::before,
.menu-nav--opened .menu-nav__toggle::after {
  content: "";
  position: absolute;
  background-color: #231f20;
  left: 17px;
  top: 25px;
  box-shadow: none;
  width: 20px;
  height: 2px;
}
.menu-nav--opened .menu-nav__toggle::before {
  transform: rotate(45deg);
}
.menu-nav--opened .menu-nav__toggle::after {
  transform: rotate(-45deg);
}
.menu-nav--closed .menu-nav__list,
.menu-nav--closed .menu-nav__list-user {
  display: none;
}
@media (min-width: 768px) {
  .menu-nav--closed .menu-nav__list,
  .menu-nav--closed .menu-nav__list-user {
    display: inherit;
  }
}
.menu-nav--nojs .menu-nav__list,
.menu-nav--nojs .menu-nav__list-user {
  display: block;
}
@media (min-width: 768px) {
  .menu-nav--nojs .menu-nav__list,
  .menu-nav--nojs .menu-nav__list-user {
    display: grid;
  }
}
.menu-nav--nojs .menu-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .menu-nav .menu-nav__toggle {
    display: none;
  }
}
.menu-nav__container {
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) {
  .menu-nav__container {
    display: grid;
    grid-template-columns: 320px 346px;
    align-items: flex-start;
  }
}
@media (min-width: 1150px) {
  .menu-nav__container {
    grid-template-columns: 701px 349px;
  }
}
.menu-nav__toggle {
  display: block;
  position: absolute;
  top: -65px;
  right: 17px;
  z-index: 2;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.menu-nav__list,
.menu-nav__list-user {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  list-style-type: none;
}
.menu-nav__link {
  position: relative;
  display: block;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  padding: 28px 0;
  width: 185px;
}
@media (min-width: 768px) {
  .menu-nav__link {
    padding-left: 0;
    height: min-content;
  }
}
.menu-nav__link:hover {
  opacity: 0.6;
}
.menu-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .menu-nav__link {
    padding: 0;
    height: min-content;
  }
}
.menu-nav__link svg {
  fill: rgba(0, 0, 0, 0.2);
  position: absolute;
  left: -36px;
}
@media (min-width: 768px) {
  .menu-nav__link svg {
    left: 33px;
  }
}
.menu-nav__link:hover svg {
  fill: #63d1bb;
}
.menu-nav__item {
  margin: 0;
  padding: 0;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  display: flex;
  justify-content: center;
}
.menu-nav__item--tablet,
.menu-nav__item--desktop {
  display: none;
}
@media (min-width: 768px) {
  .menu-nav__item--tablet {
    display: flex;
  }
}
@media (min-width: 1150px) {
  .menu-nav__item--desktop {
    display: flex;
  }
}
@media (min-width: 768px) {
  .menu-nav__item {
    box-shadow: none;
    justify-content: flex-start;
  }
  .menu-nav__item--search {
    width: 82px;
    height: 88px;
    border-left: 1px solid #ececec;
    border-right: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .menu-nav__item--search svg {
    position: static;
  }
}
@media (min-width: 768px) {
  .menu-nav__item--search span {
    display: none;
  }
}
@media (min-width: 768px) {
  .menu-nav__item a {
    font-size: 15px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
    width: auto;
  }
}
@media (min-width: 1150px) {
  .menu-nav__item:nth-child(-n + 2) .menu-nav__link {
    font-size: 17px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
  }
}
@media (min-width: 768px) {
  .menu-nav__item--search a,
  .menu-nav__item--cart a {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .menu-nav__list {
    display: grid;
    grid-template-columns: 160px 155px;
    margin-top: 88px;
    column-gap: 9px;
  }
}
@media (min-width: 1150px) {
  .menu-nav__list {
    margin-top: 0;
    width: 716px;
    grid-template-columns: 186px 211px 252px;
    grid-template-rows: 88px;
    column-gap: 34px;
  }
}
@media (min-width: 768px) {
  .menu-nav__list .menu-nav__item {
    margin-top: 22px;
  }
}
@media (min-width: 1150px) {
  .menu-nav__list .menu-nav__item:nth-child(1) {
    border-right: 1px solid #ececec;
  }
  .menu-nav__list .menu-nav__item:nth-child(2) {
    grid-column: span 2;
    padding-right: 270px;
  }
  .menu-nav__list .menu-nav__item:nth-child(-1n + 2) {
    margin-top: 0;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .menu-nav__list-user {
    display: grid;
    grid-template-columns: 75px 267px;
    grid-template-rows: 88px;
    column-gap: 5px;
  }
}
@media (min-width: 1150px) {
  .menu-nav__list-user {
    grid-template-columns: 82px 267px;
    column-gap: 0;
  }
}
@media (min-width: 768px) {
