    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: #444444;
  }
}
.menu-nav__list-user .menu-nav__item {
  display: flex;
  justify-content: flex-end;
}
.menu-nav__list-user .menu-nav__item:not(:nth-child(-1n + 2)) {
  margin-top: 20px;
}
.menu-nav__list-user .menu-nav__item:not(:nth-child(-n + 2)) {
  grid-column: span 2;
  padding-left: 35px;
}
.menu-nav__list-user .menu-nav__item--search,
.menu-nav__list-user .menu-nav__item--cart {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-nav__list-user .menu-nav__item--tablet {
  display: none;
}
@media (min-width: 768px) {
  .menu-nav__list-user .menu-nav__item--tablet {
    display: flex;
  }
}
.menu-nav__list-user .menu-nav__item--desktop {
  display: none;
}
@media (min-width: 1150px) {
  .menu-nav__list-user .menu-nav__item--desktop {
    display: flex;
  }
}
@media (min-width: 768px) {
  .menu-nav__item--cart a {
    font-size: 17px;
    line-height: 25px;
    font-weight: 700;
    color: #000000;
    padding-left: 85px;
  }
}
